*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html,
body
{ 
    width: 100%;
    height: 100%;
    
}
#main{
    position: relative;
    overflow-x: hidden;
}
@font-face {
    font-family: "ritesh";
    src: url('../static/assets/fonts/Tektur-Variable.ttf');
}
@font-face {
    font-family: 'kk';
    src: url('../static/assets/fonts/VT323-Regular.ttf');
}
img{
    object-fit: cover;
    object-position: center;
}
@media (max-width: 600px) {

#page1 #mountain #mount1{
    height: 100%;
    width: 100%;
    margin-left: -25%;
    object-fit: cover;
}
#page1 #mountain #mount1{
    height: 100%;
    width: 100%;
    margin-right: 13%;
    object-fit: cover;
}
#page1 h2{
    display: none;
}

}


.webgl
{
   width: 100%;
   height: 100%;
   overflow: hidden;
   pointer-events: none;
   position: absolute;
   z-index: 9999;
}
#main::-webkit-scrollbar{
    display: none!important;
}

#page1{
    width: 100vw;
    height: 100vh;
    background-position: center;
    z-index: 99999999999;
    overflow-x: hidden;
    background-size: cover;
    background-image: url('../static/assets/clouds/cloud-light-2.webp');
    /* background: #000; */
}
#page1 h2{
    position: absolute!important;
    font-family:ritesh;
    color: #272521;
    font-size: 15vh;
    z-index: 9999999999999;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
#page1 #mountain{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 999999999999;
}


#page1 #clouds{
 position: absolute;
 z-index: 8888;
 width: 100%;
 height: 100%;
}
#page1 #clouds #cldimg1{
    /* float: right; */
    top: 0%!important;
    right: 0%;
    height: 80% ;
    position: absolute;
}
#page1 #clouds #cldimg3{
    /* float: right; */
    top: 0%!important;
    left: 0%;
    /* height: 80% ; */
    position: absolute;
}
#page1 #clouds #cldimg2{
    /* float: right; */
    bottom: 0%!important;
    left: 0%;
    /* height: 80% ; */
    position: absolute;
}

#page1 #clouds #cldimg4{
    /* float: right; */
    bottom: 0%!important;
    right: 0%;
    /* height: 80% ; */
    position: absolute;
}
#page1 #clouds img{
    /* background: #000; */
}


#page2{
    width: 100vw;
    height: 100vh;
    background-color: red;   
    background-image: url('../static/assets/clouds/cloud-light-1.webp');
    background-size: cover;
    background-position: center;
    position: relative!important;
    z-index: 9999999999!important;
}
#page2 #clds1{
left: -10%;
top: -50%;
/* position: absolute;background:red; */

}

